import React from "react";
import { Accordion, Card, Button } from 'react-bootstrap';
import "bootstrap/dist/css/bootstrap.min.css";


export default function MovieCard({movie}){
    return (
        <div className="card--movie">
            <img className="card--image"
            src={`https://image.tmdb.org/t/p/w185_and_h278_bestv2/${movie.poster_path}`}
            alt={movie.title + ' poster'}/>

            <div className="card-content">
                <h3 className="card--title">{movie.title}</h3>
                <p><small><strong>RELEASE DATE: </strong>{movie.release_date}</small></p>
                <p><small><strong>RATING: </strong>{movie.vote_average}</small></p>
            </div>

            <div>
                <Accordion>
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                            <strong>More Info!</strong>
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="0">
                        <Card.Body>
                            <p className="card--desc"><strong>OVERVIEW: </strong>{movie.overview}</p>
                            <p><small><strong>POPULARITY:</strong> {movie.popularity}</small></p>
                            <p><small><strong>VOTE COUNT:</strong> {movie.vote_count}</small></p>
                            <p><small><strong>VOTE AVERAGE:</strong> {movie.vote_average}</small></p>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                </Accordion>
            </div>
        </div>
        
    )
}
