import React, {useState} from "react";
import MovieCard from './movieCard.js';
export default function SearchMovies(){


    //state for user input query, and movies to be displayed
    //Initial value will be an empty string
    //Return an array for two things, first will be the actual state and track information, second will be a function to update that state
    const [query, setQuery] = useState('');

    //Create state for movies and update them when appropiate
    const [movies, setMovies] = useState([]);


    const searchMovies = async (e) => {
        e.preventDefault();
        console.log("Searching...");

       
        const url = `https://api.themoviedb.org/3/search/movie?api_key=8607e20bbb736a686daeeb093977d957&language=en-US&query=${query}&page=1&include_adult=false`;
        
        try{
            const res = await fetch(url);
            const data = await res.json();
            console.log(data.results);
            setMovies(data.results);
        }catch(err){
            console.error(err);
        }
        
    }


    return (
        <>
            <form className="form" onSubmit={searchMovies}>
                <label className="label" htmlFor="query">Search Movie...</label>
                <input className="input" type="text" name="query" placeholder="Enter Movie..." 
                    value={query} onChange={(e) => setQuery(e.target.value)}
                    />
                <button className="button" type="submit">Search!</button>
            </form>
            <div className="card-list">
                {movies.filter(movie => movie.poster_path).map(movie => (
                   <MovieCard movie={movie}  key={movie.id}/>
                ))}
            </div>    
        </>
    )
}